import CardList from './cardList'
import React, {Component} from 'react';
import SearchBox from './searchBox'
import './style/style.css'
import Loader from "react-loader-spinner";
import Scroll from './scroll'
import ErrorBoundary from './errorBoundary';

class App extends Component {
    constructor(){
        super()
        this.state = {
            robots: [],
            userInput: ''
        }
    }
    onChanged = (event) => {
        this.setState({userInput: event.target.value});
    }

    componentDidMount(){
        fetch('https://jsonplaceholder.typicode.com/users').then(Response => Response.json()).then(user => this.setState({robots:user}))
    }
    render(){
        const filteredRobot = this.state.robots.filter(robots => {
            return robots.name.toLowerCase().includes(this.state.userInput.toLowerCase());
        })
        if (this.state.robots === 0){
            return <div className="loader-spinner">
                        <Loader color="blueviolet" height={100} width={100}/>
                  </div>
        }
       return (
            <div className="tc">
                <h1>Robot</h1>
                <div>
                     <SearchBox onTextChanged={this.onChanged}/>
                </div>
                <Scroll>
                        <ErrorBoundary>
                            <CardList robots={filteredRobot}/>  
                        </ErrorBoundary>  
                </Scroll>,
            </div>
       )
    }
}

export default App;

