import React from 'react';

const Card = ({name, email, id}) => {
    return (
        <div className="ba dib br3 bg-purple pa1 ma2 grow shadow-5">
            <img src={`http://robohash.org/robot${id}?150x150`} alt="idk" />
            <div className="tc ">
                <h2>{name}</h2>
                <p>{email}</p>
            </div>
        </div>
    )
}
export default Card;